  justify-content: center;
  align-items: center;
  gap: 56px;
  display: flex;
}

.header__nav-link {
  letter-spacing: .01em;
  color: #2b2b2b;
  padding-top: 56px;
  padding-bottom: 67px;
  font-family: Caviar Dreams;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16;
  display: block;
}

.header__drop-menu {
  margin-top: 56px;
  margin-bottom: 67px;
